.profile__name {
  margin: 0 0 8px;
  font-size: 42px;
  font-weight: 500;
  line-height: 48px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 505px;
}

@media (max-width: 1279px) {
  .profile__name {
    font-size: 27px;
    font-weight: 500;
    line-height: 33px;
    max-width: 222px;
  }
}
