.profile__job {
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 545px;
}

@media (max-width: 1279px) {
  .profile__job {
    max-width: 282px;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    text-align: center;
  }
}